@tailwind base;
@tailwind components;
@tailwind utilities;


@layer base {
  input{
     @apply block  rounded-md border-0 px-3.5 py-2 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6
  }
  input[type="button"]{
      @apply  bg-white hover:bg-gray-100  border border-gray-200 focus:ring-4 focus:outline-none  focus:ring-gray-100 font-medium rounded-lg text-sm px-5 py-2.5 text-center inline-flex items-center  mr-2 mb-2 justify-center 
  }
  input[type=number] {
      @apply text-right
  }
  input[type=text] {
    @apply text-left
  }
   h1 {
    @apply text-2xl uppercase text-center my-5 ;

  }
  h2 {
    @apply text-xl;
  }
  table {
    @apply min-w-full text-center text-sm font-light
  }
  table thead tr{
    @apply bg-slate-900 w-full text-white uppercase text-center m-5
  }
}
.Overlay {
    position: fixed; inset: 0px;
     background-color: rgba(24, 23, 23, 0.75);
  }
#root{
display: flex; flex-wrap: wrap; min-height: 100vh; 
}
.center-vertical{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;  
  
}
.center{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  height: 400px;
}
.show{
  display: block !important;
}
.hidde{
  display:none !important;
}
.frame{
-webkit-box-shadow: 3px 3px 15px 8px rgba(0,0,0,0.97); 
box-shadow: 3px 3px 15px 8px rgba(0,0,0,0.97);
}